import "./FindTeam.scss"
import { FindTeamCard } from "../../components/findTeamCard/FindTeamCard"

function FindTeam() {
    return (
        <div className="findteam" id="findTeam">
            <div className="ft__container">
                <div className="ft__header">
                    <div className="ft__header-row">
                        <div className="ft__header-title"><b> </b>  </div>
                        <button className="ft__header-button">  <img src={require("../../UI/utils/img/findteamarrow.png")} alt="#" className="ft__button-arrow"/> </button>
                    </div>
                </div>
                <div className="ft__main">
                    <div className="ft__main-row">
                        <div className="ft__main-card1">
                            <FindTeamCard 
                                avatar={require("../../UI/utils/img/eugeneavatar.png")}
                                name={""}
                                surname={""}
                                skill__1={""}
                                skill__2={"Project"}
                                skill__3={"SCRUM-"}
                                age={"19 "}
                                city={".-"}
                                description={" !  full-stack      .        ,     !"}
                            />
                        </div>
                        <div className="ft__main-card2">
                            <FindTeamCard 
                                avatar={require("../../UI/utils/img/mashaavatar.png")}
                                name={""}
                                surname={""}
                                skill__1={"-"}
                                skill__2={"Figma"}
                                skill__3={"Tilda"}
                                age={"18 "}
                                city={".-"}
                                description={" !        full-stack   SCRUM-)"}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export {FindTeam}
